
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import PopupPicker from 'c%/PopupPicker.vue'

const emptyObject = {
bid: 0,

name: '',
tel: '',
email: '',
sex: 1,
birth_time: 0,
grade: '',
remark: '',
is_valid: 0,
assigned_eid: 0,
recommend_sid: 0,
recommend_note: ''
}

export default{
mixins: [ common ],
components: {
Cell,
Group,
XInput,
XButton,
XTextarea,
PopupPicker
},
data () {
return {
info: util.copy(emptyObject),
}
},
computed: {
disabled () {
let result = this.info.name==''||this.info.tel.length!=11

return result
},
bid () {
let sid = this.info.recommend_sid,
students = this.$store.state.user.info.students,
bid = 0

if(sid > 0&&students.length > 0){
let obj = students.find(s=>s.sid==sid)

bid = obj?obj.bid:0
}

return bid
},
students () {
let list = this.$store.state.user.info.students||[],
result = []
if(list.length > 0){
list.forEach(l=>{
result.push({
key: l.sid,
value: l.student_name
})
})
}

return result
}
},
mounted () {
this.info.recommend_sid = this.sid$

},
methods: {
submit () {
let [uri,method,msg] = ['market_clues','post','添加成功！']
this.info.bid = this.bid

this.$rest(uri)[method](this.info)
.success(data=>{
this.toast(msg,'success')
setTimeout(()=>{
this.$router.push({path:'/my/recommend_record'})
},800)
}).error(response=>{
this.toast(response.body.message||'操作失败~','warn')
})
}
}
}




